body {
  display: flex;
  justify-content: center;
}

button {
  display: block;

  margin: 10px;
  padding: 20px;
  min-width: 200px;
  
  background-color: lightskyblue;
  border-radius: 10px;
  cursor: pointer;
}

button:disabled {
  background-color: #808080;
  cursor: default;
}